export class ColumnDefinition {
    constructor(name, type) {
        this.definition = "";
        this.definition = `\`${name}\` ${type}`;
    }
    static create(name, type) {
        return new ColumnDefinition(name, type);
    }
    nullable(nullable) {
        this.definition += nullable ? " NULL" : " NOT NULL";
        return this;
    }
    default(value) {
        if (value !== undefined)
            this.definition += ` DEFAULT ${value}`;
        return this;
    }
    unsigned(unsigned) {
        if (unsigned)
            this.definition += " UNSIGNED";
        return this;
    }
    autoIncrement(autoIncrement) {
        if (autoIncrement)
            this.definition += " AUTO_INCREMENT";
        return this;
    }
    zeroFill(zerofill) {
        if (zerofill)
            this.definition += " ZEROFILL";
        return this;
    }
    primaryKey(primaryKey) {
        if (primaryKey)
            this.definition += " PRIMARY KEY";
        return this;
    }
    after(after) {
        if (after)
            this.definition += ` AFTER \`${after}\``;
        return this;
    }
    get() {
        return this.definition;
    }
}
